import { useEffect, useState } from "react";
import { getAuth } from "../../services/authService";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";
import NoUser from "../../assets/images/NoUser.png";
import { findUser } from "../../services/userService";
import styles from './MyPage.module.css'

const MyPage = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [userInfo, setUserInfo] = useState({});

  useEffect(() => {
    const varityAuth = async () => {
      try {
        const auth = await getAuth();
        const userData = await findUser({ id: auth.id });
        setUserInfo(userData.data);
      } catch (error) {
        toast.error(<h1>  .</h1>, {
          position: "top-center",
          autoClose: 2000,
        });
        navigate("/");
      } finally {
        setLoading(false);
      }
    };
    varityAuth();
  }, [navigate]);

  const deleteUser = ()=>{
    if(window.confirm("  ?       .")){
      alert("  ")
    } else {
      alert("  ")
    }
  }
  if(loading){
    return <h1>Loading...</h1>
  }
  return (
    <div className={styles.Page}>
      <div className={styles.Wrap}>
        <div className={styles.Head}>
          <img src={userInfo.profileImg || NoUser} alt="" />
          <p>{userInfo.username}   </p>
        </div>
        <div className={styles.Info}>
          <p className={styles.Title}></p>
          <div>
            <p></p>
            <p>{userInfo.email}</p>
          </div>
          <div>
            <p></p>
            <p>{userInfo.phone.slice(0,3)}-{userInfo.phone.slice(3,7)}-{userInfo.phone.slice(7,11)}</p>
          </div>
          <div>
            <p> </p>
            <p>{userInfo.createdAt}</p>
          </div>
        </div>
        <div className={styles.Service}>
          <p className={styles.Title}></p>
          <div onClick={()=>{
            navigate('/mypage/counsel')
          }}>
            <p> </p>
            <p>{">"}</p>
          </div>
          <div onClick={()=>{
            navigate('/mypage/password')
          }}>
            <p> </p>
            <p>{">"}</p>
          </div>
          <div onClick={deleteUser}>
            <p> </p>
            <p>{">"}</p>
          </div>
        </div>
      </div>
    </div>
  );
};
export default MyPage;
