import "./Header.css";
import { Link } from "react-router-dom";
import Logo from "../../assets/images/Logo.png";
import hamburger_icon from "../../components/images/hamburger_icon.png";
import x_icon from "../../components/images/x_icon.png";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { logout } from "../../redux/userSlice";
import { toast } from "react-toastify";
import NoUser from "../../assets/images/NoUser.png";
import { useEffect, useRef, useState } from "react";

const Header = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user.value);
  const [userControl, setUserControl] = useState(false);

  const logoutBtn = () => {
    dispatch(logout());
    setUserControl(false);
    localStorage.removeItem("Tokens");
    toast.success(<h3> .</h3>, {
      position: "top-center",
      autoClose: 2000,
    });
    navigate("/");
  };
  return (
    <div className="Header">
      <img
        className="hamburger_icon"
        src={hamburger_icon}
        alt="hamburger_icon"
      />
      <img className="x_icon" src={x_icon} alt="x_icon" />
      <div className="HeaderWrap">
        <div className="HeaderLeft">
          <a href="/">
            <img className="Logo" src={Logo} alt="Logo" />
          </a>

          <div className="Menu">
            {/* <Link to="/"></Link> */}
            {/* <Link to="/lawyers"></Link> */}
            <Link to="/counsel/list"> </Link>
            <Link to="/counsel"> </Link>
            <Link to="/counsel/search"> </Link>
          </div>
        </div>
        <div className="RightNav">
          {user.isLogined ? (
            <div
              className="ProfileImg"
              onClick={() => {
                setUserControl(!userControl);
              }}
            >
              <img src={user.profileImg || NoUser} alt="profileImg" />
            </div>
          ) : (
            <Link className="LoginBtn" to="login">
              
            </Link>
          )}
          {userControl && (
            <UserControl
              userName={user.username}
              userPhone={user.phone}
              logoutBtn={logoutBtn}
              setUserControl={setUserControl}
            />
          )}
        </div>
      </div>
    </div>
  );
};

const UserControl = (props) => {
  return (
    <div className="UserControl">
      <div className="UserControlHead">
        <p>{props.userName} </p>
      </div>
      <Link
        className="UserControlMenu"
        to={`/counsel/list?phone=${props.userPhone}`}
        onClick={() => {
          props.setUserControl(false);
        }}
      >
         
      </Link>
      <Link
        className="UserControlMenu"
        to="/mypage"
        onClick={() => {
          props.setUserControl(false);
        }}
      >
        
      </Link>
      <div className="UserControlMenu" onClick={props.logoutBtn}>
        
      </div>
    </div>
  );
};
export default Header;
