import { useEffect, useState } from "react";
import { getAuth } from "../../services/authService";
import { useDispatch } from "react-redux";
import { logout } from "../../redux/userSlice";
import { toast } from "react-toastify";
import { createCounsel } from "../../services/counselService";
import { useNavigate } from "react-router-dom";
import { createAIComment } from "../../services/commentService";
import Button from "../../components/Button/Button";
import Text from "../../components/Text/Text";

const Counsel = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [inputs, setInputs] = useState({
    title: "",
    name: "",
    phone: "",
    password: "",
    desc: "",
  });

  const { title, name, phone, password, desc } = inputs;

  const handleInputs = (e) => {
    setInputs({
      ...inputs,
      [e.target.name]: e.target.value,
    });
  };

  const sendBtn = async () => {
    try {
      const response = await createCounsel({
        title,
        name,
        phone,
        password,
        desc,
      });
      if (response.data.message === "Success") {
        createAIComment({
          content: desc,
          counselId: response.data.newCounsel._id,
        });
        toast.success(<h3>  .</h3>, {
          position: "top-center",
          autoClose: 2000,
        });
        navigate("/counsel/confirm", { state: desc });
      }
    } catch (error) {
      toast.error(error.response.data.message, {
        position: "top-center",
        autoClose: 2000,
      });
    }
  };

  useEffect(() => {
    try {
      const Tokens = JSON.parse(localStorage.getItem("Tokens"));
      if (Tokens) {
        const verifyAuth = async () => {
          await getAuth();
          const response = await getAuth();
          if (response.message === "No authorized!  .") {
            dispatch(logout());
            localStorage.removeItem("Tokens");
            return;
          }
        };
        verifyAuth();
      }
    } catch (error) {
      dispatch(logout());
      localStorage.removeItem("Tokens");
    }
  }, []);

  return (
    <div className="Page">
      <div className="Wrap">
        <Text
          textAlign={"center"}
          fontSize={22}
          fontWeight={400}
          fontColor={"#000"}
          margin={"60px 0 15px 0"}
          text={" \n  ?"}
        />
        <Text
          textAlign={"center"}
          fontSize={30}
          fontWeight={700}
          fontColor={"#000"}
          margin={"0 0 15px 0"}
          text={" "}
        />
        <div className="Block">
          <h2 className="Title">
             <span className="Red">*</span>
          </h2>
          <input
            className="Input"
            name="title"
            value={title}
            type="text"
            placeholder="1   ."
            onChange={handleInputs}
          />
        </div>
        <div className="Block">
          <h2 className="Title">
             <span className="Red">*</span>
          </h2>
          <input
            className="Input"
            name="name"
            value={name}
            type="text"
            placeholder=" ."
            onChange={handleInputs}
          />
        </div>
        <div className="Block">
          <h2 className="Title">
             <span className="Red">*</span>
          </h2>
          <input
            className="Input"
            type="text"
            name="phone"
            value={phone}
            placeholder="'-'    "
            onChange={handleInputs}
          />
        </div>
        <div className="Block">
          <h2 className="Title">
             <span className="Gray">{"( 4)"}</span>{" "}
            <span className="Red">*</span>
          </h2>
          <input
            className="Input"
            type="text"
            name="password"
            value={password}
            placeholder=" ."
            onChange={handleInputs}
          />
        </div>
        <div className="Block">
          <h2 className="Title">
              <span className="Red">*</span>
          </h2>
          <textarea
            className="TextArea"
            name="desc"
            value={desc}
            placeholder="    ."
            onChange={handleInputs}
          />
        </div>
        <Button
          buttonName={""}
          buttonColor={"#00C126"}
          buttonTextColor={"#FFF"}
          buttonMargin={"10px 0"}
          pressButton={sendBtn}
        />
      </div>
    </div>
  );
};

export default Counsel;
