import { useEffect, useState } from "react";
import { findAllCounsel, findUserCounsel } from "../../services/counselService";
import { useLocation, useNavigate } from "react-router-dom";
import Text from "../../components/Text/Text";
import IconAI from "../../assets/images/IconAI.png";
import IconLawyer from "../../assets/images/IconLawyer.png";
import { toast } from "react-toastify";
import Pagination from "../../components/Pagination/Pagination";
import { getAuth } from "../../services/authService";

const CounselUser = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const queryParams = new URLSearchParams(location.search);
  const phone = queryParams.get("phone");
  let limit = 10
  const [currentPage, setCurrentPage] = useState(1);
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const [post, setPost] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const auth = await getAuth();
        const response = await findUserCounsel({ userId: auth.id });
        let dataToSet = response.data;
        if(dataToSet.length === 0){
          toast.error(<h1>  .</h1>, {
            position: "top-center",
            autoClose: 2000,
          });
          navigate('/mypage')
          return
        }
        setData(dataToSet);
        setPost(dataToSet.slice((currentPage - 1) * limit, (currentPage) * limit));
      } catch (error) {
        console.error(error);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [phone, navigate, currentPage, limit]);

  return (
    <div className="Page">
      <div className="Wrap">
        <div className="CounselHead">
          <Text
            textAlign={"left"}
            fontSize={25}
            fontWeight={600}
            fontColor={"#000"}
            margin={"35px 0"}
            text={"  "}
          />
          <Text
            textAlign={"left"}
            fontSize={25}
            fontWeight={600}
            fontColor={"#000"}
            margin={"35px 0"}
            text={data && ` ${data.length}`}
          />
        </div>
        <div className="CounselTable">
          {!loading &&
            post.map((action, index) => (
              <div className="CounselList">
                <a
                  className="CounselTitle"
                  href={`/counsel/${action._id}`}
                  key={index}
                >
                  <p>{action.title}</p>
                </a>
                <div>
                  {!action.comment && <p> </p>}
                  {action.comment === "AIComment" && (
                    <div className="CommentStatus">
                      <img src={IconAI} alt="IconAI" />
                      <Text
                        textAlign={"left"}
                        fontSize={14}
                        fontWeight={700}
                        fontColor={"#00C126"}
                        margin={"0"}
                        text={""}
                      />
                    </div>
                  )}
                  {action.comment === "LawyerComment" && (
                    <div className="CommentStatus">
                      <img src={IconLawyer} alt="IconLawyer" />
                      <Text
                        textAlign={"left"}
                        fontSize={14}
                        fontWeight={700}
                        fontColor={"#000"}
                        margin={"0"}
                        text={""}
                      />
                    </div>
                  )}
                </div>
              </div>
            ))}
        </div>
        <Pagination
          totalPosts={data.length}
          postsPerPage={limit}
          currentPage={currentPage}
          setPage={setCurrentPage}
        />
      </div>
    </div>
  );
};
export default CounselUser;
