import { useState } from "react";
import "./Comment.css";
import { deleteComment, updateComment } from "../../services/commentService";
import { toast } from "react-toastify";
import { faL } from "@fortawesome/free-solid-svg-icons";

const Comment = (props) => {
  const [mode, setMode] = useState(false);
  const [content, setContent] = useState("");
  const modifyBtn = () => {
    setMode(!mode);
    setContent(props.content);
  };
  const updateBtn = async () => {
    try {
      props.setLoading(true);
      await updateComment({
        commentId: props.commentId,
        name: props.name,
        content,
        userId: props.userId,
        counselId: props.counselId,
      });
      toast.success(<h3>  .</h3>, {
        position: "top-center",
        autoClose: 2000,
      });
      setMode(false);
    } catch (error) {
      toast.error(error.response.data.message, {
        position: "top-center",
        autoClose: 2000,
      });
    } finally {
      props.setLoading(false);
    }
  };
  const deleteBtn = async () => {
    try {
      if (window.confirm(" ?")) {
        props.setLoading(true);
        await deleteComment({
          commentId: props.commentId,
        });
        toast.success(<h3>  .</h3>, {
          position: "top-center",
          autoClose: 2000,
        });
        setMode(false);
      } else {
        return;
      }
    } catch (error) {
      toast.error(error.response.data.message, {
        position: "top-center",
        autoClose: 2000,
      });
    } finally {
      props.setLoading(false);
    }
  };
  return (
    <div className="CommentWrap">
      <div
        className="CommentHead"
        style={
          props.name === " AI"
            ? { backgroundColor: "rgba(0, 193, 38, 0.15)", color: "#00C126" }
            : { backgroundColor: "#F4F4F4" }
        }
      >
        <p>{props.name} {props.userRole === "Admin" && ""} {props.userRole === "Lawyer" && ""}</p>
        <p>{props.createdAt}</p>
        {/* {props.updatedAt && <p>{props.updatedAt}</p>} */}
      </div>

      {props.userId === props.currentUser.id && (
        <div>
          <button onClick={() => modifyBtn({ data: props.content })}>
            
          </button>
          <button onClick={deleteBtn}></button>
        </div>
      )}

      <div className="CommentBody">
        {mode ? (
          <>
            <input
              type="text"
              value={content}
              onChange={(e) => {
                setContent(e.target.value);
              }}
            />
            <button onClick={updateBtn}> </button>
          </>
        ) : (
          <div
            className="ComentBodyDesc"
            style={
              props.name === " AI"
                ? {
                    borderLeft: "1px solid #00C126",
                  }
                : { borderLeft: "1px solid #000" }
            }
          >
            <p
              style={
                props.name === " AI"
                  ? {
                      fontWeight: 400,
                    }
                  : { fontWeight: 700 }
              }
            >
              {props.content}
            </p>
          </div>
        )}
      </div>
    </div>
  );
};

export default Comment;
