import { useState } from "react";
import "./Login.css";
import { faCheck } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import SocialKakao from "../../components/SocialKakao/SocialKakao";
import { toast } from "react-toastify";
import { signIn } from "../../services/authService";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { login } from "../../redux/userSlice";

const Login = () => {
  const [category, setCategory] = useState("User");
  return (
    <div className="Login">
      <div className="CategoryWrap">
        <div
          className={category === "User" ? "CategoryActive" : "Category"}
          onClick={() => {
            setCategory("User");
          }}
        >
          
        </div>
        <div
          className={category === "Lawyer" ? "CategoryActive" : "Category"}
          onClick={() => {
            setCategory("Lawyer");
          }}
        >
           
        </div>
      </div>
      {category === "User" ? <UserLogin /> : <LawyerLogin />}
    </div>
  );
};

const UserLogin = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [isAutoLogin, setisAutoLogin] = useState(false);
  const [inputs, setInputs] = useState({
    email: "",
    password: "",
  });
  const { email, password } = inputs;
  const sendBtn = async () => {
    try {
      const response = await signIn({ email, password, isAutoLogin });
      dispatch(
        login({
          isLogined: true,
          isAutoLogin,
          email: response.email,
          username: response.username,
          profileImg: response.profileImg,
          roles: response.roles,
        })
      );
      toast.success(<h3>{response.username} </h3>, {
        position: "top-center",
        autoClose: 2000,
      });
      navigate("/");
    } catch (error) {
      toast.error(error.response.data.message, {
        position: "top-center",
        autoClose: 2000,
      });
    }
  };
  return (
    <>
      <form>
        <div className="LoginInput">
          <label htmlFor="email"></label>
          <input
            type="text"
            name="email"
            id="email"
            placeholder=" ."
            value={email}
            onChange={(e) => {
              setInputs({
                ...inputs,
                [e.target.name]: e.target.value,
              });
            }}
          />
        </div>
        <div className="LoginInput">
          <label htmlFor="password"></label>
          <input
            type="password"
            name="password"
            id="password"
            placeholder=" ."
            value={password}
            autoComplete="off"
            onChange={(e) => {
              setInputs({
                ...inputs,
                [e.target.name]: e.target.value,
              });
            }}
          />
        </div>
      </form>
      <div className="CheckFindWrap">
        <div
          className="CheckWrap"
          onClick={() => {
            setisAutoLogin(!isAutoLogin);
          }}
        >
          <FontAwesomeIcon
            className={isAutoLogin ? "CheckIconActive" : "CheckIcon"}
            icon={faCheck}
          />
          <p className={isAutoLogin ? "CheckLabelActive" : "CheckLabel"}>
              
          </p>
        </div>
        <a className="FindBtn" href="/">
          / 
        </a>
      </div>
      <button className="FetchBtn" onClick={sendBtn}>
        
      </button>
      <div className="Or">
        <p></p>
      </div>
      <SocialKakao />
      <div className="CheckFindWrap MT-30">
        <p className="RegistWord">   ?</p>
        <a className="RegistBtn" href="/regist">
          
        </a>
      </div>
    </>
  );
};

const LawyerLogin = () => {
  const [isAutoLogin, setisAutoLogin] = useState(false);
  return (
    <>
      <div className="LoginInput">
        <label htmlFor="id"></label>
        <input
          type="text"
          name="id"
          id="id"
          placeholder=" ."
        />
      </div>
      <div className="LoginInput">
        <label htmlFor="password"></label>
        <input
          type="password"
          name="password"
          id="password"
          placeholder=" ."
        />
      </div>
      <div className="CheckFindWrap">
        <div
          className="CheckWrap"
          onClick={() => {
            setisAutoLogin(!isAutoLogin);
          }}
        >
          <FontAwesomeIcon
            className={isAutoLogin ? "CheckIconActive" : "CheckIcon"}
            icon={faCheck}
          />
          <p className={isAutoLogin ? "CheckLabelActive" : "CheckLabel"}>
              
          </p>
        </div>
        <a className="FindBtn" href="/">
          / 
        </a>
      </div>
      <button className="FetchBtn"></button>
      <div className="CheckFindWrap MT-30">
        <p className="RegistWord">   ?</p>
        <a className="RegistBtn" href="/regist/lawyer">
            
        </a>
      </div>
    </>
  );
};

export default Login;
