import { useNavigate } from "react-router-dom";
import * as Yup from "yup";
import { Formik } from "formik";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { createUser } from "../../services/userService";

const LawyerRegist = () => {
  const navigate = useNavigate();
  const validationSchema = Yup.object().shape({
    email: Yup.string()
      .email("   !")
      .required(" !"),
    phone: Yup.string()
      .matches(/^\d+$/, " !")
      .required(" !"),
    callNumber: Yup.string().matches(/^\d+$/, " !"),
    registNumber: Yup.string()
      .matches(/^\d+$/, " !")
      .required(" !"),
    password: Yup.string()
      .min(8, "  8 ")
      .max(16, "  16!")
      .required(" !")
      .matches(
        /^(?=.*[a-zA-Z])(?=.*[0-9])(?=.*[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?])[^\s]*$/,
        ", ,      !"
      ),
    password2: Yup.string()
      .oneOf([Yup.ref("password"), null], "  !")
      .required("  !"),
  });

  const submit = async (values) => {
    const { email, username, password, phone, callNumber, registNumber } =
      values;
    await createUser({
      email,
      username,
      password,
      phone,
      callNumber,
      registNumber,
    })
      .then(() => {
        toast.success(<h3>   .      1~3  .</h3>, {
          position: "top-center",
          autoClose: 2000,
        });
        setTimeout(() => {
          navigate("/login");
        }, 2000);
      })
      .catch((err) =>
        toast.error(err.response.data.message + "", {
          position: "top-center",
        })
      );
  };
  return (
    <Formik
      initialValues={{
        email: "",
        username: "",
        phone: "",
        callNumber: "",
        registNumber: "",
        password: "",
        password2: "",
      }}
      validationSchema={validationSchema}
      onSubmit={submit}
      validateOnMount={true}
    >
      {({ values, handleSubmit, handleChange, errors, isValid, dirty }) => (
        <div className="Login">
          <ToastContainer />
          <h2 className="Title"></h2>
          <form onSubmit={handleSubmit} autoComplete="off">
            <div className="LoginInput">
              <label htmlFor="">
                 <span className="Red">*</span>
              </label>
              <input
                type="text"
                name="email"
                value={values.email}
                onChange={handleChange}
              />
              <div className="Message">
                {!values.email ? null : <p>{errors.email}</p>}
              </div>
            </div>
            <div className="LoginInput">
              <label htmlFor="username">
                 <span className="Red">*</span>
              </label>
              <input
                type="text"
                name="username"
                value={values.username}
                onChange={handleChange}
              />
              <div className="Message">
                {!values.username ? null : <p>{errors.username}</p>}
              </div>
            </div>
            <div className="LoginInput">
              <label htmlFor="phone">
                 <span className="Red">*</span>
              </label>
              <input
                type="text"
                name="phone"
                value={values.phone}
                onChange={handleChange}
              />
              <div className="Message">
                {!values.phone ? null : <p>{errors.phone}</p>}
              </div>
            </div>
            <div className="LoginInput">
              <label htmlFor="callNumber"> </label>
              <input
                type="text"
                name="callNumber"
                value={values.callNumber}
                onChange={handleChange}
              />
              <div className="Message">
                {!values.callNumber ? null : <p>{errors.callNumber}</p>}
              </div>
            </div>
            <div className="LoginInput">
              <label htmlFor="registNumber">
                   <span className="Red">*</span>
              </label>
              <input
                type="text"
                name="registNumber"
                value={values.registNumber}
                onChange={handleChange}
              />
              <div className="Message">
                {!values.registNumber ? null : <p>{errors.registNumber}</p>}
              </div>
            </div>
            <div className="LoginInput">
              <label htmlFor="">
                 <span className="Red">*</span>
              </label>
              <input
                type="password"
                name="password"
                className="Input"
                value={values.password}
                onChange={handleChange}
              />
              <div className="Message NT">
                {!values.password ? null : <p>{errors.password}</p>}
              </div>
            </div>
            <div className="LoginInput">
              <label htmlFor="">
                  <span className="Red">*</span>
              </label>
              <input
                type="password"
                name="password2"
                className="Input"
                value={values.password2}
                onChange={handleChange}
              />
              <div className="Message NT">
                {!values.password2 ? null : <p>{errors.password2}</p>}
              </div>
            </div>
            {!(isValid && dirty) ? (
              <div className="DisabledButton"></div>
            ) : (
              <button
                type="submit"
                className="FetchBtn"
                disabled={!(isValid && dirty)}
              >
                
              </button>
            )}
          </form>
        </div>
      )}
    </Formik>
  );
};
export default LawyerRegist;
